@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200&family=Poppins:wght@100;500&family=Raleway:wght@200&family=Roboto:wght@100&family=Space+Grotesk:wght@500&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

*{
    font-family: "Space Grotesk", sans-serif;
}
.front-card{
    position: absolute;
    top: 185px;
    background: url("./images/bg-card-front.png") no-repeat center/cover;
    height: 230px;
    width: 320px;
    border-radius: 10px;
    z-index: 20;
}
.back-card{
    position: absolute;
    right: 20pm startpx;
    background: url("./images/bg-card-back.png") no-repeat center/cover;
    height: 230px;
    width: 320px;
    border-radius: 10px;
}

.back-card p{
    top: 6.15rem;
}

form, .thank-you{
    margin-top: 350px;
}
form label{
    @apply block uppercase tracking-wider text-slate-800 mb-2 text-sm;
}
form input{
    @apply border-2 border-slate-300 py-3 px-4 rounded outline-none w-full;
}

.btn {
    background-color: hsl(278, 68%, 11%);
    @apply py-3 px-6 rounded shadow text-white text-base tracking-wide lg:text-lg;

}

@media (min-width: 768px){
    .front-card, .back-card{
        height: 300px;
        width: 500px;
        border-radius: 20px;
    }

    .back-card p{
        top: 8.3rem;
    }

    form, .thank-you {
        margin-top: 500px;
    }
}

@media (min-width: 1024px) {

    .front-card, .back-card{
        position: relative;
    }
    
    .front-card {
        top: 50px;
    }

    .back-card {
        right: 0;
    }

    form, .thank-you {
        margin-top: 0;
    }
}